.calendar-list-page {
  padding-top: 100px;
  padding-left: 25px;
}
.calendars-list {
  list-style: none;
  font-size: 24px;
  text-transform: uppercase;
  font-weight: bold;
  padding: 0;
  margin: 0;
  margin-bottom: 60px;
  position: relative;
}

.calendars-list__item {
  padding-left: 50px;
  display: flex;
  align-items: center;
  min-height: 60px;
}

.calendars-list__shape {
  position: absolute;
  content: '';
  width: 34px;
  height: 34px;
  background-color: black;
  left: 0;
}

.calendars-list__link {
  text-decoration: none;
  color: inherit;
}
.button {
  display: inline-block;
  padding-left: 20px;
  padding-right: 50px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 24px;
  font-weight: bold;
  text-transform: uppercase;
  border: 4px solid black;
  background-color: white;
  text-decoration: none;
  color: #000;
}
.create-calendar-page {
  padding-left: 20px;
  padding-right: 20px;
}

.create-calendar-page__label {
  display: inline-block;
  margin-bottom: 5px;
}

.create-calendar-page__input {
  display: inline-block;
  border: 4px solid black;
  width: 100%;
  margin-bottom: 55px;
  height: 52px;
  font-size: inherit;
  padding-left: 10px;
  padding-bottom: 5px;
  text-transform: inherit;
  font-weight: inherit;
}
.page-header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: white;
}

.page-header--calendar {
  box-shadow: 0 2px 5px gray;
}

.page-header__container {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding-top: 45px;
  padding-bottom: 25px;
  padding-left: 20px;
  padding-right: 20px;
}

.page-header__arrow-back {
  padding: 0;
  border: none;
}

.page-header__title {
  margin: 0;
  margin-bottom: 20px;
}

.page-header__wrapper-right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.calendar-table {
  width: 100%;
  border-collapse: collapse;
  box-shadow: 0 -1px 5px gray;
}

.calendar-table__td {
  width: calc(100% / 7);
  height: 58px;
  border: 1px solid black;
  padding: 0;
  padding-left: 5px;
  padding-top: 5px;
  vertical-align: top;
  font-size: 14px;
  background-color: white;
}

.calendar-table__td--checked {
  background-color: black;
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-auto-rows: calc(100vw / 7);
  font-size: 12px;
}

@media only screen and (min-width: 500px) {
  .calendar-grid {
    grid-auto-rows: calc(500px / 7);
  }
}

.calendar-cell {
  border: 1px solid black;
  padding: 0;
  padding-left: 5px;
  vertical-align: top;
  font-size: 14px;
  background-color: white;
  padding-top: 5px;
}

.calendar-cell--checked {
  background: black;
  color: white;
}

.calendar-cell--today-unchecked {
  background:
          linear-gradient(to top left,
          rgba(0,0,0,0) 0%,
          rgba(0,0,0,0) calc(50% - 0.8px),
          rgba(0,0,0,1) 50%,
          rgba(0,0,0,0) calc(50% + 0.8px),
          rgba(0,0,0,0) 100%),
          linear-gradient(to top right,
          rgba(0,0,0,0) 0%,
          rgba(0,0,0,0) calc(50% - 0.8px),
          rgba(0,0,0,1) 50%,
          rgba(0,0,0,0) calc(50% + 0.8px),
          rgba(0,0,0,0) 100%);
}

.calendar-cell--today-checked {
  background: black;
}




* {

  box-sizing: border-box;
  margin: 0;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  font-weight: bold;
  text-transform: uppercase;

}

.container {
  max-width: 500px;
  margin: 0 auto;
  box-shadow: 2px 0 30px black, -2px 0 30px black;
  height: 100vh;
}
